    animation: float 4s ease-in-out infinite;
    animation-delay: 1s;
  }
  .animate-float-delay-3 {
    animation: float 4s ease-in-out infinite;
    animation-delay: 2s;
  }
  .animate-float-delay-4 {
    animation: float 4s ease-in-out infinite;
    animation-delay: 3s;
  }
  .animate-fade-in-up {
    animation: fadeInUp 0.8s ease-out forwards;
  }
  .cta-button {
    transition: transform 0.2s, box-shadow 0.2s;
  }
