    transform: scale(1.05);
    box-shadow: 0 0 30px hsla(265, 90%, 60%, 0.4);
  }
  .phone-glow {
    background: radial-gradient(
      circle at center,
      hsla(265, 90%, 60%, 0.15) 0%,
      transparent 70%
    );
  }
